/*===============================
=           Footer            =
===============================*/
#page-footer {
    background-color: #1d1d1d;
    padding-top: 60px;
    .container {
        @media (min-width:1550px) {
            width: 1230px;
            max-width: 1230px;
        }
    }
    .footer-block {
        padding-bottom: 60px;
        @include bp(sm-down) {
            padding-bottom: 40px;
        }
        .footer-title {
            display: block;
            color: #fff;
            font-size: 2.250em;
            font-family: $robo-reg;
            line-height: 1.2;
            margin-bottom: 30px;
            @include bp(desktop) {
                font-size: 1.8em;
            }
            @include bp(lg) {
                font-size: 1.6em;
            }
            @include bp(md) {
                font-size: 1.4em;
            }
            @include bp(sm-down) {
                font-family: $robo-bold;
                font-size: 14px;
                text-transform: uppercase;
                text-align: center;
                margin-bottom: 20px;
                letter-spacing: .1em;
            }
        }
        p {
            @include regular-text;
            color: #a4a4a4;
        }
        .contact-btn {
            a {
                font-family: $robo-bold;
                font-size: 12px;
                letter-spacing: .1em;
                display: inline-block;
                color: #fff;
                background-color: $red;
                text-align: center;
                padding: 10px 15px;
                border-radius: 30px;
                line-height: 1;
                &:hover {
                   background-color: $dark;
                }
            }
        }
        .footer-socials {
            margin-top: 15px;
            margin-bottom: 15px;
            ul {
                li {
                    a {
                        color: #a4a4a4;
                        &:hover {
                            color: #fff;
                        }
                    }
                }
            }
        }
        .footer-sitemap {
            ul {
                li {
                    margin-bottom: 20px;
                    @include bp(sm-down) {
                        margin-bottom: 10px;
                    }
                    a {
                        display: inline-block;
                        @include regular-text;
                        color: #fff;
                        margin-bottom: 0;
                        &:hover {
                            color: #a4a4a4;
                        }
                    }
                }
            }
        }
        .footer-tags {
            a {
                display: inline-block;
                margin: 5px 3px;
                padding: 0 8px;
                border: 1px solid #363636;
                border-radius: 2px;
                font-size: 13px!important;
                transition-duration: .2s;
                color: #a4a4a4;
                &:hover {
                    box-shadow: 0 2px 6px rgba(0,0,0, 0.3);
                    -webkit-transform: translateY(-2px);
                    -moz-transform: translateY(-2px);
                    -ms-transform: translateY(-2px);
                    -o-transform: translateY(-2px);
                    transform: translateY(-2px);
                    color: #fff;
                }
            }
        }
    }
    .col {
        @include bp(sm-down) {
            min-width: 100%;        
        }
    }
   
    .copybar {
        padding-top: 20px;
        padding-bottom: 20px;
        border-top: 1px solid #363636;
        @include bp(sm-down) {
            text-align: center;
        }
        small {
            font-family: $robo-reg;
            font-size: 14px;
            line-height: 1.5;
            color: #a4a4a4;
        }
    }
}

/*=======End of Footer ========*/

// Fixed CTA
#fixed-cta {
    position: fixed;
    bottom:10px;
    right: 10px;
    z-index:9999;
    @include bp(sm-down) {
        width: 100%;
    }
    i {
        font-size: 32px;
        color:$white;
        position: absolute;
        left:30px;
        top:12px;
        animation: pulsate 750ms infinite;
        @include bp(sm-down) {
            top:5px;
        }
    }
    a {
        display: inline-block;
        background:$red;
        border-radius: 9999px;
        padding:20px;
        display: flex;
        min-width: 200px;
        padding-top: 15px;
        padding-bottom: 15px;
        border: 4px solid #ff8288;
        position: relative;
        padding-left: 80px;
        padding-right: 40px;
        @include bp(sm-down) {
            margin:0px auto;
            width: 280px;
            padding-top: 10px;
            padding-bottom: 10px;
        }
        &:hover {
            background: #202c42;
        }
        small {
            display: block;
            font-size: 14px;
            color:$white;
            text-transform: uppercase;
            line-height: 1.5
            ;
        }
        span {
            display: block;
            font-size: 20px;
            font-family: $robo-bold;
            color:$white;
            font-size: 22px;
            line-height: 1;
        }
    }
}